.container {
  flex: 1;
  margin: 10px;
  font-family: "TitilliumWeb-SemiBold";
  padding: 8px 10px;
  outline: none;
  border-radius: 50px;
  border-color: transparent;
}

.container::placeholder {
  font-family: "TitilliumWeb-Italic";
  font-size: 1.6rem;
  overflow: hidden;
}

@media screen and (max-width: 480px) {
  .container {
    padding: 5px 10px;
  }

  .container::placeholder {
    font-family: "TitilliumWeb-Italic";
    font-size: 1.4rem;
  }
}

@media screen and (max-width: 320px) {
  .container {
    margin: 5px;
    padding: 2px 5px;
  }

  .container::placeholder {
    font-family: "TitilliumWeb-Italic";
    font-size: 1.2rem;
  }
}
